function sum(a, b){
    return a + b;
}

// Mtodo Principal
// No caso s poderam ser exportados apenas um default por arquivo:
// Ao declarar o import  possivel alterar o nome e no ser apenas o nome da `f`
// No precisa utilizar as chaves.
export default sum;

// NamedExport
// Varios exports dentro do mesmo arquivo, mas s pode chamar com o mesmo nome 
// e o import precisa das chaves { sub }
export function sub(a, b){
    return a - b;
}
// Tambem  possivel fazer essa mesma funo com o export separado, ou seja
//  bom para exportar varios mtodos com apenas uma declarao export.
function mult(a, b){
    return a * b;
}

function div(a, b){
    return a / b;
}
// Veja abaixo que alem de multiplos exports tambm  possivel exportar com alias vejamos:
export { mult as multiplicar, div };

// Tambm  possivel criar um export de tudo `export * as utils`;


